@import "reset";
@import "typography";
@import "animations";

@import "~rfs/scss";

.layer {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    max-width: 100%;
    height: 100vh;

    overflow: hidden;
}

.layer1 {
    @extend .layer;

    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    font-family: 'Marker', sans-serif;

    background: pink;
}

.layer2 {
    @extend .layer;

}

.bottom-corner-image {
    position: fixed;
    bottom: 0;
    height: auto;
    width: 40%;
    max-width: 300px;
}

.bottom-corner-image-left {
    @extend .bottom-corner-image;
    left: 0;
}

.bottom-corner-image-right {
    @extend .bottom-corner-image;
    right: 0;
}

.vote-line {
    display: block;

    @include font-size(6rem);
}

.float-box {
    .float-image {
        position: absolute;
        top: 0;
        left: 0;

    }

    &:nth-child(odd) {
        .float-image {
            @extend .swing;
        }
    }

    &:nth-child(even) {
        .float-image {
            @extend .pulse;
        }
    }
}



