@font-face {
    font-family: 'Marker';
    src: url('../assets/fonts/MarkerBoldRegular.eot');
    src: url('../assets/fonts/MarkerBoldRegular.eot?#iefix') format('embedded-opentype'),
    url('../assets/fonts/MarkerBoldRegular.woff2') format('woff2'),
    url('../assets/fonts/MarkerBoldRegular.woff') format('woff'),
    url('../assets/fonts/MarkerBoldRegular.ttf') format('truetype'),
    url('../assets/fonts/MarkerBoldRegular.svg#MarkerBoldRegular') format('svg');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}