// Pulse
.pulse {
    $pulse-duration: 1s;
    -moz-animation: $pulse-duration ease 0s normal none infinite pulse;
    -webkit-animation: pulse $pulse-duration infinite ease-in-out;
}

@-moz-keyframes pulse {
    0% {
        transform: scale(1,1);
    }
    50% {
        transform: scale(1.2,1.2);
    }
    100% {
        transform: scale(1,1);
    }
}

@-webkit-keyframes pulse {
    0% {
        transform: scale(1,1);
    }
    50% {
        transform: scale(1.2,1.2);
    }
    100% {
        transform: scale(1,1);
    }
}

// Swing
.swing {
    $swing-duration: 2s;
    -moz-animation: $swing-duration ease 0s normal none infinite swing;
    -moz-transform-origin: center top;
    -webkit-animation: swing $swing-duration infinite ease-in-out;
    -webkit-transform-origin: top;
}

@-moz-keyframes swing {
    0% {
        -moz-transform: rotate(-15deg);
    }
    50% {
        -moz-transform: rotate(15deg);
    }
    100% {
        -moz-transform: rotate(-15deg);
    }
}
@-webkit-keyframes swing {
    0% {
        -webkit-transform: rotate(-15deg);
    }
    50% {
        -webkit-transform: rotate(15deg);
    }
    100% {
        -webkit-transform: rotate(-15deg);
    }
}